import React from "react";

import AIFileIcon from "./AI";
import CSSFileIcon from "./CSS";
import CSVFileIcon from "./CSV";
import DOCFileIcon from "./DOC";
import EPSFileIcon from "./EPS";
import GIFFileIcon from "./GIF";
import HTMLFileIcon from "./HTML";
import JPEGFileIcon from "./JPEG";
import MP4FileIcon from "./MP4";
import PDFFileIcon from "./PDF";
import PNGFileIcon from "./PNG";
import PPTFileIcon from "./PPT";
import PSDFileIcon from "./PSD";
import SVGFileIcon from "./SVG";
import TXTFileIcon from "./TXT";
import XLSFileIcon from "./XLS";


export const FileIconsArray = ["AI", "CSS", "CSV", "DOC", "EPS", "GIF", "HTML", "JPEG"
	, "MP4", "PDF", "PNG", "PPT", "PSD", "SVG", "TXT", "XLS"] as const;

export type FileIcons = typeof FileIconsArray[number];

interface FileIconProps {
	fileName: FileIcons
}

const fileIcon: Record<FileIcons, JSX.Element> = {
	AI: <AIFileIcon />,
	CSS: <CSSFileIcon />,
	CSV: <CSVFileIcon />,
	DOC: <DOCFileIcon />,
	EPS: <EPSFileIcon />,
	GIF: <GIFFileIcon />,
	HTML: <HTMLFileIcon />,
	JPEG: <JPEGFileIcon />,
	MP4: <MP4FileIcon />,
	PDF: <PDFFileIcon />,
	PNG: <PNGFileIcon />,
	PPT: <PPTFileIcon />,
	PSD: <PSDFileIcon />,
	SVG: <SVGFileIcon />,
	TXT: <TXTFileIcon />,
	XLS: <XLSFileIcon />,
};

const FileIcon = ({ fileName }: FileIconProps) => fileIcon[fileName];


FileIcon.displayName = "FileIcon";
export default FileIcon;
